import React from 'react';
import Img from 'gatsby-image';

import { screen, colors } from '../constants';

export default function BlockLogos({ pretitle, title, data }) {
  return (
    <section className="block-logos">
      {pretitle && (
        <div className="app__pretitle block-logos__pretitle">{pretitle}</div>
      )}
      {title && <div className="app__title block-logos__title">{title}</div>}
      <div className="block-logos__content">
        {data.map(({ image, name, offsetTop = 0 }, index) => (
          <Img
            key={index}
            fluid={image}
            imgStyle={{ objectFit: 'contain' }}
            alt={name}
            className="app__image block-logos__image"
            style={{ top: offsetTop }}
          />
        ))}
      </div>
      <style jsx>{`
        .block-logos {
          margin: 24px 0 60px;
        }
        .block-logos__content {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
        }

        @media (max-width: ${screen.mobileMax}px) {
          .block-logos__content {
            align-items: center;
            flex-direction: column;
          }
        }

        .block-logos__pretitle,
        .block-logos__title {
          text-align: center;
        }
      `}</style>
      <style jsx global>{`
        .block-logos__image {
          margin: 25px;
          flex: 1;
        }
        @media (max-width: ${screen.mobileMax}px) {
          .block-logos__image {
            min-width: 150px;
          }
        }
      `}</style>
    </section>
  );
}
