import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../components/Layout';
import Footer from '../components/Footer';
import Banner from '../components/Banner';
import Container from '../components/Container';
import BlockHeaderHomepage from '../components/BlockHeaderHomepage';
import BlockHowItWorks from '../components/BlockHowItWorks';
import SpaceDivider from '../components/SpaceDivider';
import Block3PartIllo from '../components/Block3PartIllo';
import BlockLogos from '../components/BlockLogos';
import BlockCTA from '../components/BlockCTA';

import TimeIcon from '../components/icons/Time';
import ShieldIcon from '../components/icons/Shield';
import DropIcon from '../components/icons/Drop';

import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  query HomePageQuery {
    howItWorksImages: allFile(
      filter: { relativeDirectory: { eq: "how-it-works" } }
    ) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    banner: file(relativePath: { eq: "banners/why-sell-banner.png" }) {
      childImageSharp {
        fluid {
          originalImg
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
  }
`;

export default function IndexPage({ data }) {
  const howItWorksImages = edgesByName(data.howItWorksImages);
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout>
      <GatsbySeo
        title="Grow your b2b sales with automated credit terms"
        description="Resolve is the easiest way to increase your customers' buying power via credit lines and Net Payment Terms. Trusted by the world's most innovative companies."
      />
      <Banner showImage />
      <Container>
        <BlockHeaderHomepage
          desc="Resolve is the easiest way to increase your customers' buying power via credit lines and Net Payment Terms."
          primaryButtonText="Get a Demo"
          primaryButtonTo="/"
        />
        <BlockHowItWorks
          title="Resolve fits your b2b sales flow"
          subtitle="simple and automated"
          data={[
            {
              id: '01',
              question: 'Offer net terms',
              answer:
                'Easily extend net terms financing on your website using our net terms payment gateway.',
              image: howItWorksImages['mills'],
            },
            {
              id: '02',
              question: 'Get more customers',
              answer:
                'Buyers apply through a simple application and get approved for dedicated payment terms (net 30-60) within one business day.',
              image: howItWorksImages['tell-us-about'],
            },
            {
              id: '03',
              question: 'Make sales',
              answer:
                'You receive more sales and your buyers receive a personalized confirmation of their payment.',
              image: howItWorksImages['order-confirmation'],
            },
            {
              id: '04',
              question: 'Get paid',
              answer:
                'After a purchase is placed, Resolve automatically transfers the funds to your bank account. We handle customer invoicing, billing, and collections.',
              image: howItWorksImages['get-paid'],
            },
          ]}
        />
        <SpaceDivider height={100} />
        <Block3PartIllo
          title="Why sell with Resolve"
          rows={[
            [
              {
                icon: <TimeIcon />,
                title: 'Expand average order size',
                content: (
                  <div>
                    Give your buyers the credit they need to expand their
                    budgets, allowing them to pull the trigger on even larger
                    purchases.
                  </div>
                ),
              },
              null,
              null,
            ],
            [
              {
                icon: <ShieldIcon width="28" height="28" />,
                title: 'Increase repeat purchases',
                content: (
                  <div>
                    Provide your buyers with a dedicated line of credit,
                    encouraging them to purchase with you again and again.
                  </div>
                ),
              },
              {
                icon: <DropIcon width="28" height="28" />,
                title: 'Convert new and old leads into buyers',
                content: (
                  <div>
                    Our credit approvals help you reach out to new or existing
                    customers who haven't started purchasing yet.{' '}
                  </div>
                ),
              },
              null,
            ],
          ]}
          backgroundImage={data.banner.childImageSharp.fluid.originalImg}
        />
        <SpaceDivider height={150} />
        <BlockLogos
          pretitle="Trusted by the world's most innovative companies"
          data={[
            {
              image: companies['CozyEarth'],
              name: 'CozyEarth',
              offsetTop: 0,
            },
            {
              image: companies['Hyperikon'],
              name: 'Hyperikon',
            },
            {
              image: companies['Interior Define'],
              name: 'Interior Define',
              offsetTop: 0,
            },
            {
              image: companies['SimpleTire'],
              name: 'SimpleTire',
              offsetTop: 0,
            },
            {
              image: companies['Real Thread'],
              name: 'Real Thread',
              offsetTop: -6,
            },
          ]}
        />
        <SpaceDivider height={100} />
        <BlockCTA
          pretitle="Ready to get started?"
          title="Take your b2b sales to the next level."
          primaryButtonText="Get started"
          primaryButtonTo=""
        />
      </Container>
      <Footer darkSkin />
    </Layout>
  );
}
